import React from 'react';
import NavigationItem from "./NavigationItem/NavigationItem";
import '../../../assets/css/bootstrap.min.css';

const navigationItems = (props) => (
    <div className={"navbar-collapse collapse"+ props.show} id="navbarResponsive">
        <ul className="navbar-nav">
            <NavigationItem link="/" exact>About Me</NavigationItem>
            <NavigationItem link="/" exact>Blogs</NavigationItem>
            <NavigationItem link="/pdg" >PDG</NavigationItem>
            <NavigationItem link="/" exact>Contact</NavigationItem>
        </ul>
    </div>
);

export default navigationItems;