import React, {Fragment} from 'react';
import '../../assets/css/bootstrap.min.css';

const pdg = (props) => {
    // const signUpHandler = async (event) => {
    //     event.preventDefault();
    //     let signup = document.getElementById("signup");
    //     let formData = new FormData(signup);
    //     console.log(...formData);
    //     const api = "localhost/post";
    //     let response = await fetch(api, {
    //         method: 'POST',
    //         body: formData
    //     });
    //     let result = await response.json();
    //     console.log(result);
    // }
    return(
        <Fragment>
            <div className="bs-component">
                <h2>Political Debates Group</h2>
                <p>
                    Welcome to a free speaking and listening group!<br/>
                    We are a group of debators who like to discuss politics and its implications on society. We are of the thought
                    that only meaningful discussions and conversations can bring out the true impact of a political vision and decision
                    on society. If you are a like minded, rational and logical person who likes to listen and discuss political
                    discourses then you can join us by signing up with your email below.
                </p>
            </div>
            <div className="bs-component" style={{marginTop: "50px"}}>
                <form id="signup" action="localhost/post" method="POST">
                    <fieldset>
                        <div className="form-group">
                            <label>Email Address</label>
                            <input type="email" className="form-control" id="email" aria-describedby="emailHelp"
                            placeholder="Enter email"/>
                            <small id="emailHelp" className="form-text text-muted">
                                We will never share your email with anyone else.
                            </small>
                        </div>
                        <div className="form-group">
                            <label>Any thoughts?</label>
                            <textarea className="form-control" id="thoughts" rows="3"></textarea>
                        </div>
                        <button type="submit" className="btn btn-success">Sign Up</button>
                    </fieldset>
                </form>
            </div>
        </Fragment>
);
}
export default pdg;